import logo from './logo.svg';
import './App.css';
import { useCallback, useEffect } from 'react';
import { confetti } from "https://cdn.jsdelivr.net/npm/tsparticles-confetti/+esm";
import { send } from 'feather-icons';
function App() {
  const duration1 = 15 * 5000,
  animationEnd1 = Date.now() + duration1;

let skew = 10;

function randomInRange(min, max) {
  return Math.random() * (max - min) + min;
}



const SocialMediaItem = ({ icon, text,link }) => {
  return (
    <a href={link}>
    <div style={{ cursor:'pointer',display: 'flex', alignItems: 'center', marginRight: '20px', borderRadius: '0.3rem', background: '#ffffff1c', padding: '0.4rem 0.8rem', border: '1px solid #8080804f' }}>
      <img src={icon} alt={text} style={{ width: '30px', marginRight: '15px', filter:'invert(1)' }} />
      <span style={{fontSize:'1.5rem'}}>{text}</span>
    </div>
    </a>
  );
};

const run = () => {
  (function frame() {
    const timeLeft = animationEnd1 - Date.now(),
      ticks = Math.max(200, 500 * (timeLeft / duration1));

    skew = Math.max(0.8, skew - 0.001);

    confetti({
      particleCount: 1,
      startVelocity: 0,
      ticks: ticks,
      origin: {
        x: Math.random(),
        // since particles fall down, skew start toward the top
        y: Math.random() * skew - 0.2
      },
      colors: ["#ffffff"],
      shapes: ["square"],
      gravity: randomInRange(0.4, 0.6),
      scalar: randomInRange(0.4, 1),
      drift: randomInRange(-0.4, 0.4)
    });

    if (timeLeft > 0) {
      requestAnimationFrame(frame);
    }
  })();
};

const duration2 = 10 * 1000,
  animationEnd2 = Date.now() + duration2;

const run2 = () => {
  (function frame() {
    const timeLeft = animationEnd2 - Date.now();

    confetti({
      particleCount: 2,
      angle: 60,
      spread: 55,
      origin: { x: 0 },
      colors: ["#bb0000", "#ffffff"]
    });

    confetti({
      particleCount: 2,
      angle: 120,
      spread: 55,
      origin: { x: 1 },
      colors: ["#bb0000", "#ffffff"]
    });

    if (timeLeft > 0) {
      requestAnimationFrame(frame);
    }
  })();
};

useEffect(() => {
  setTimeout(() => {
    run();
  }, 10000);
  run2();
}, []);

  return (
    <>
    <img src='./ss.png' style={{opacity: '0.6', height: 'auto', width: '97vw', filter: 'blur(3px)  brightness(0.4)', objectFit: 'contain', zIndex: '-2', position: 'absolute'}}></img>
    <div className='glowAura'></div>
    <div className='gratitudeBox'>
    <h1 className='designerText' style={{fontSize: '1.5rem'}}>CROWDSOURCE</h1>
      <img style={{height:'4vh', marginBottom:'10vh'}} src='./powerby.png'></img>
      <h1 className='designerText' style={{fontSize: '1.5rem'}}> Thankyou to all the 52069 Contributors </h1>
      <h1 className='designerText' style={{marginTop:0}}>Testnet Phase 1 Complete - Onward to Phase 2!</h1>
      <p style={{width: '80%', textAlign: 'center', fontSize: '1.3rem'}}>We're immensely thankful to our dedicated community for your support and contributions. Your enthusiasm and feedback have been pivotal as we wrapped up the first phase of our Testnet.</p>
      {/* <p style={{marginTop:0,width: '50%',fontStyle:'italic', textAlign: 'center',opacity:0.5, fontSize: '1.3rem'}}>Don't miss out on our journey ahead! Stay informed about Phase 2 and all future updates by following us on Twitter.</p> */}
      <div style={{ display: 'flex', justifyContent: 'space-around', width: '70%', margin: '4vh 0px', opacity: '0.9', flexWrap: 'wrap', gap: '1rem 0'}}>
      <SocialMediaItem icon="/twitter.svg" text="Follow on Twitter" link="https://twitter.com/clusterprotocol" />
      <SocialMediaItem icon="/blog.svg" text="Read Our Blogs" link="https://clusterprotocol.io/blogs" />
      <SocialMediaItem icon="/telegram.svg" text="Join us on Telegram" link="https://t.me/clusterprotocolchat" />
    </div> <div className='btnAlpha'><h1 style={{fontFamily:'sephora', fontSize:'1.5rem'}}>Follow Us For More Updates</h1></div>  
    </div>
    </>
  );
}

export default App;

